.bg-1{
    background-image: url(../../images//bg/bg-1.jpg);
}
.bg-image{
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.hero-banner{
    display: flex;
    align-items: center;
    min-height: 100vh;
}
.banner-content{
    position: relative;
    h1{
        color: #fff;
        span{
            color: #14141f;
            background: #fff;
            background-size: 100% 100%;
            -webkit-background-clip: text;
            -webkit-text-stroke: 3px transparent;
        }
    }
    p{
        font-size: 22px;
        line-height: 1.75;
        text-transform: capitalize;
    }
}
.group-btn{ 
    margin-left: -10px;
    margin-right: -10px;
    a{
        margin: 0 10px;
    }
}



.shape {
    position: absolute;
    z-index: 2;
}
.shape-2 {
    right: 22%;
    bottom: 5%;
}
.shape-3{
    top: -53px;
    left: 33%;
}
.slider-wrapper{
    position: relative;
}