.btn{
    font-size: 22px;
    padding: 14px 50px;
    display: inline-flex;
    align-items: center;
    color: #fff;
    border-radius: 50px;
    transition: 0.3s;
   
    i{
        padding-right: 8px;
        
    }
    &:hover{
        color: #fff;
        transform: translateY(-3px);
    }
}
.btn-small{
    font-size: 16px;
    padding: 10px 25px;
}
.btn-outline{
    border: 1px solid $body-color;
    &:hover{
       border-color: #fff;
       color: $color-primary;
    }

}
.btn-gradient{
    background: linear-gradient(97.3deg, #FF512F 0%, #DD2476 100%);
}