
.mainmenu-nav .mainmenu {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0 0 0 30px;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .mainmenu-nav .mainmenu li {
    position: relative;
    margin: 0 18px;
  }
  .mainmenu-nav .mainmenu li a {
    font-weight: 500;
    padding: 28px 12px;
    font-size: 18px;
    display: block;
    border-radius: 3px;
  }
  @media only screen and (min-width: 1200px) and (max-width: 1599px) {
    .mainmenu-nav .mainmenu li a {
      padding: 28px 6px;
    }
  }
  .mainmenu-nav .mainmenu li .submenu {
    min-width: 230px;
    height: auto;
    position: absolute;
    z-index: 99;
    top: 90%;
    left: 0;
    z-index: 90;
    opacity: 0;
    visibility: hidden;
    text-align: left;
    padding: 12px 0;
    transition: 0.3s;
    background-color: #000;
    border-radius: 0 0 10px 10px;
    border-left: 1px solid #ffffff14;
    border-bottom: 1px solid #ffffff14;
    border-right: 1px solid #ffffff14;
  }
  .mainmenu-nav .mainmenu li .submenu li {
      margin: 0;
    a {
        font-weight: 500;
        padding: 8px 14px;
        font-size: 16px;
        display: block;
        color: #acacac;
        margin: 0 10px;
        border-radius: 4px;
        transition: 0.3s;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
  }
  .mainmenu-nav .mainmenu li .submenu li a svg,
  .mainmenu-nav .mainmenu li .submenu li a i {
    stroke-width: 2px;
    width: 22px;
    height: auto;
    background: transparent;
    padding: 2px;
    border-radius: 2px;
    transition: 0.3s;
    width: 22px;
    left: -10px;
    position: relative;

    opacity: 0;
    margin: 0 -3px;
  }
  .mainmenu-nav .mainmenu li .submenu li a:hover {
    color: $color-primary;
    margin-left: 5px;
  }
  .mainmenu-nav .mainmenu li .submenu li a:hover svg,
  .mainmenu-nav .mainmenu li .submenu li a:hover i {
    opacity: 1;
    left: 0;
  }
  .mainmenu-nav .mainmenu li:hover .submenu {
    opacity: 1;
    visibility: visible;
    top: 100%;
  }

  .mainmenu-nav .mainmenu li.has-droupdown > a {
    position: relative;
  }
  .mainmenu-nav .mainmenu li.has-droupdown > a::after {
    content: "\EA4E";
    position: absolute;
    top: 50%;
    font-family: 'remixicon' !important;
    right: -12px;
    font-size: 22px;
    transform: translateY(-50%);
  }
  @media only screen and (min-width: 1200px) and (max-width: 1599px) {
    .mainmenu-nav .mainmenu li.has-droupdown > a::after {
      right: -11px;
    }
  }
  
  .rn-header.header--fixed.position-absolute {
    background: #00000042;
    backdrop-filter: blur(1px);
  }

  .rn-header.header--fixed.position-absolute
    .mainmenu-nav
    .mainmenu
    li.nav-item
    .submenu {
    background: #00000042 !important;
  }
  .rn-header.header--fixed.position-absolute
    .mainmenu-nav
    .mainmenu
    li.nav-item
    .submenu
    li
    a {
    color: #c7c7cf;
  }
  .rn-header.header--fixed.position-absolute
    .mainmenu-nav
    .mainmenu
    li.nav-item
    .submenu
    li
    a
    svg,
  .rn-header.header--fixed.position-absolute
    .mainmenu-nav
    .mainmenu
    li.nav-item
    .submenu
    li
    a
    i {
    color: #c7c7cf;
  }
  
  .rn-header.header--fixed.sticky {
    background: transparent;
    backdrop-filter: blur(15px);
  }

  .rn-header.header--fixed.sticky .mainmenu-nav .mainmenu li.nav-item .submenu {
    
  }
  
  span.rn-badge-card {
    margin-left: 7px;
    padding: 1px 7px;
    font-weight: 400;
    position: relative;
    z-index: 2;
    font-size: 14px;
    
  }
  span.rn-badge-card::before {
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    background: linear-gradient(
        95deg,
        #000000 15%,
        #0074b9 45%,
        #0400ff 75%,
        #2a1b95
      )
      95%/200% 100%;
    z-index: -1;
    border-radius: 100px;
    opacity: 0.5;
  }
  
