
.explore-style-two{
    .explore-thumb {
        background: #343444;
        padding: 30px;
        display: inline-block;
        border-radius: 50px;
        img{
            border-radius: 50px;
          }
    }
    .explore-content{
        background: #FFFFFF;
        padding: 17px;
        backdrop-filter: blur(25px);
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 301px;
        position: absolute;
        bottom: 60px;
        right: 12%;
        .btn{
            padding-left: 18px;
            padding-right: 18px;
        }
        .price{
            padding-right: 15px;
            span{
                font-size: 12px;
                line-height: 18px;
                font-weight: 500;
                margin-bottom: 5px;
                color: #A1A1A1;
                display: inline-block;
            }
            h6{
                font-size: 16px;
                font-weight: 500;
                line-height: 23px;
                font-family: $font-1;
                margin: 0;
            }
        }
    
    }
}