.header-fixed{
    background: transparent;
    position: absolute;
    width: 100%;
    left: 0;
    top: 40px;
}

// header right
.header-right-inner{
    display: flex;
    align-items: center;
    margin-bottom: 0;
    i{
        font-size: 22px;
        color: $body-color;
        
    }
    .btn-gradient{
        i{
            color: #fff;
        }
    }
    li{
        &:not(:first-child){
            margin-left: 30px;
        }
        &:last-child{
            margin-left: 20px;
        }
    }
}
// nitification bar
.notification-bar a {
    width: 50px;
    height: 50px;
    border: 1px solid $body-color;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    position: relative;
    .ni-badge{
        font-size: 14px;
        font-weight: 500;
        background: $color-primary;
        color: #fff;
        position: absolute;
        right: -15px;
        top: 0;
        z-index: 3;
        min-width: 36px;
        line-height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }
}